* {
  margin: 0;
}

body {
  overflow: hidden;
}

.NavBox {
  top: 0;
  position: relative;
  background-color: #f0f0f0;
  justify-content: space-between;
  display: flex;
  height: 110px;
  align-items: center;
  max-width: 100vw;
  min-width: 100vw;
}

.carrossel {
  position: relative;
  height: 75vh;
  width: 100vw;
  overflow: hidden;
  outline: 7px solid rgb(190, 15, 15);
}

.carrossel ul {
  position: absolute;
  height: 75vh;
  width: 300vw;
  display: flex;
  list-style: none;
}

.carrossel li {
  position: relative;
  -webkit-animation: slide 30s infinite ease-in-out;
          animation: slide 30s infinite ease-in-out;
}

@-webkit-keyframes slide {
  0% {
    left: 0px;
  }
  23% {
    left: 0px;
  }
  33% {
    left: -1920px;
  }
  56% {
    left: -1920px;
  }
  66% {
    left: -3840px;
  }
  96% {
    left: -3840px;
  }
  100% {
    left: 0px;
  }
}

@keyframes slide {
  0% {
    left: 0px;
  }
  23% {
    left: 0px;
  }
  33% {
    left: -1920px;
  }
  56% {
    left: -1920px;
  }
  66% {
    left: -3840px;
  }
  96% {
    left: -3840px;
  }
  100% {
    left: 0px;
  }
}
.divtext {
  position: absolute;
  margin-top: 30vh;
  margin-left: 50vw;
  transform: translate(-50%, -50%);
}

.text1 {
  text-align: center;
  font-size: 54px;
  font-weight: 600;
}

.text2 {
  text-align: center;
