$FirstColor: rgb(190, 15, 15);
$SecondColor: #f0f0f0;
$ThirdColor: rgb(0, 0, 0);

footer{
    background-color: $SecondColor;
    justify-content: space-between;
    display: flex;
    align-items: center;
    max-width: 100vw;
    min-width: 100vw;
    min-height: 15vh;
}

footer p{
    position: absolute;
    font-size: 14px;
    font-weight: 500;
    transform: translate(-50%, -50%);
    right: 0;
    bottom: 0%;
}

.footerico{
    height: 20px;
    width: 20px;
}

footer ul{
    position: absolute;
    display: flex;
    align-items: center;
    list-style: none;
    padding: 15px;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -50%);
}

footer li{
    margin-left: 50px;
}