nav {
  margin: 0;
  top: 0;
  position: fixed;
  background-color: #f0f0f0;
  justify-content: space-between;
  padding: 20px 30px;
  display: flex;
  height: 110px;
  align-items: center;
  max-width: 100vw;
  min-width: 100vw;
}

header {
  justify-content: space-between;
  padding: 20px 30px;
  display: flex;
  align-items: center;
}

nav ul {
  display: flex;
  align-items: center;
  list-style: none;
  padding: 10px;
}

nav li {
  margin-left: 10px;
}

nav li a {
  color: rgb(190, 15, 15);
  text-decoration: none;
  font-size: clamp(1.2vw, 1.2vw, 1vw + 2em);
  font-weight: 600;
  padding: 8px 1vw;
  border-radius: 10px;
  transition: 0.3s;
}

nav li a:hover {
  color: #f0f0f0;
  background-color: rgb(190, 15, 15);
}

